import React from  'react'
import './ImageLinkForm.css'
const ImageLinkForm = ({inputChange,buttonSubmit})=> {
    return(
        <div>
            <p className='f3'>
                {'Detect faces in the picture. Give it a try.'}
            </p>
            <div className='center'>
                <div className='form center pa4 br3 shadow-5'>
                    <input className='f4 pa2 w-70' type='text' placeholder='URL' onChange={inputChange}/>
                    <button className='w-30 grow f4 link ph3 pv2 dib white bg-light-purple' onClick={buttonSubmit}>Detect</button> 
                </div>
            </div>
        </div>
    )
}
export default ImageLinkForm;